@import "vars";
@import "vendor/reset";

html {
    font-size: 62.5%; // (100% * 10px / 16px = 62.5 | 1rem = 10px)
}

body {
    background-color: #181818;

    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji"; // набор системных шрифтов
    font-size: 1.4rem;
    line-height: 1.5;
    letter-spacing: 0.02rem;
    color: $white;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}


/* Blocks */
@import "blocks/page";
@import "blocks/header";
@import "blocks/sidebar";
@import "blocks/main";
@import "blocks/container";
@import "blocks/profile";
@import "blocks/social";
@import "blocks/btn";









